.tambah {
  position: fixed;
  right: 1.25rem;
  bottom: 1.25rem;
}
@media screen and (min-width: 64rem) {
  .tambah {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--warna-putih);
    padding: 1rem 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
.tambah button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.75rem;
  height: 3.75rem;
  background-color: var(--warna-biru);
  border-radius: 1.25rem;
  border: none;
  transition: all 0.5s ease;
}
.tambah button .icon {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tambah button .icon img {
  width: 1.875rem;
}
.tambah button:hover {
  background-color: var(--warna-biru-tua);
}